  border-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.button-menu .button-menu__close, .button-menu.is-open .button-menu__burger {
  display: none;
}

.button-menu.is-open .button-menu__close {
  display: block;
}

.mobile-menu {
  z-index: 1;
  width: 250px;
  height: 100%;
  background-color: #e17992;
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  transition: transform .25s linear;
  display: none;
  position: fixed;
  top: 0;
}

.mobile-menu.is-open {
  display: block;
  transform: translateX(100%);
}

.button-hero-header {
  width: 209px;
